
.first-screen {
  position: relative;
  width: 100%;
  height: 463px;
  margin: 0 auto;
  overflow: hidden;
}

.rectangle-2 {
  position: absolute;
  width: 100%; 
  height: 100%;
  left: 0;
  top: 0;
  background: #F5E0E5;
}

.rectangle-3 {
  position: absolute;
  width: 65%; 
  height: 100%; 
  right: 0; 
  top: 0;
  background-size: cover;
  background-position: center;
}

.banner-content {
  position: absolute;
  left: 275px;
  top: 100px;
  z-index: 10;
  color: #333;
}

.banner-content h1 {
  font-size: 32px;
  font-weight: 700;
  color: #333333;
  margin-bottom: 20px;
  line-height: 1.2;
}

.banner-content p {
  font-size: 21px;
  color: #666666;
  margin-bottom: 25px;
}

.banner-button {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 10px;
  width: 246px;
  height: 41px;
  background-color: #F5A3B7;
  box-shadow: 0px 4px 20px rgba(245, 163, 183, 0.3);
  border: none;
  border-radius: 4px;
  color: #ffffff;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.banner-button:hover {
  background-color: #e58ba6;
}